@import url(https://fonts.googleapis.com/css?family=Baloo+Bhai|Source+Sans+Pro:300,400,700&display=swap);
body {
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
  box-sizing: border-box;
  background: -webkit-linear-gradient(350deg, rgba(250, 214, 195, 0.8) 30%, #b0eae8 120%);
  background: linear-gradient(100deg, rgba(250, 214, 195, 0.8) 30%, #b0eae8 120%); }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }


.Navbar__head {
  font-family: 'Baloo Bhai', cursive; }

.Dog__img {
  width: 75%;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  -webkit-transition: all .2s;
  transition: all .2s;
  border-radius: 50%;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3); }

.Dog:hover img {
  -webkit-filter: none;
          filter: none; }

.Dog:hover .Dog__name-link::after {
  width: 100%;
  top: 34px;
  left: 0; }

.Dog__name-link {
  text-decoration: none;
  border: 4px solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 2rem;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.2em;
  text-align: center;
  font-size: 1.4rem;
  margin-right: .7rem;
  position: relative; }
  .Dog__name-link:hover {
    text-decoration: none;
    color: inherit; }
  .Dog__name-link::after {
    width: 0;
    height: 4px;
    display: block;
    background-color: #fff;
    content: "";
    position: absolute;
    top: 34px;
    left: 50%;
    -webkit-transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
    transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1); }

