          filter: grayscale(1);
  -webkit-transition: all .2s;
  transition: all .2s; }

.DogDetails__card:hover .DogDetails__img {
  -webkit-filter: none;
          filter: none; }

.DogDetails__card {
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3); }

